<template>
  <div class="blog-custom-actions text-center">
    <i class="glyphicon glyphicon-pencil" @click="edit()"></i>  
  </div>
</template>

<script>
export default {
  props: {
    rowData: {
      type: Object,
      required: true
    },
    rowIndex: {
      type: Number
    }
  },
  methods: {
    edit () {
      const id = this.rowData.id
      this.$router.push({ name: 'update-blog', params: { id } })
    }
  }
}
</script>
